const scene = new THREE.Scene();


const camera = new THREE.PerspectiveCamera(75,innerWidth / innerHeight, 1, 1000)


const renderer = new THREE.WebGLRenderer()


renderer.setSize(innerWidth, innerHeight)
renderer.setPixelRatio(devicePixelRatio)
document.body.append(renderer.domElement)



const defaultControlsPosition = new THREE.Vector3(0, 4, 0);
const controls = new OrbitControls(camera, renderer.domElement);

controls.target.set(defaultControlsPosition.x, defaultControlsPosition.y, defaultControlsPosition.z);
controls.enableDamping = true;
controls.enablePan = false;
controls.enableRotate = true;
controls.enableZoom = true;
controls.maxZoom = 0;
controls.minDistance = 0;
controls.maxPolarAngle = Math.PI / 2;
controls.minPolarAngle = 0;


controls.update();



/** ________________________   LIGHTS   ________________________________ */





const light = new THREE.PointLight(0xFFFFFF, 2,20)
const defaultpositionLight = new THREE.Vector3(2, 10, 4);
light.position.set(defaultpositionLight.x,defaultpositionLight.y,defaultpositionLight.z)
scene.add(light)



const sphereSize = 1;
const pointLightHelper = new THREE.PointLightHelper( light, sphereSize );
scene.add( pointLightHelper );

const axesHelper = new THREE.AxesHelper(50);
//scene.add( axesHelper );

const light2 = new THREE.PointLight(0xFFFFFF, 2,20)
light2.position.set(-5,10,-4);
light2.name = "lampe2" ;
scene.add(light2);

const pointLightHelper2 = new THREE.PointLightHelper( light2, sphereSize );
scene.add( pointLightHelper2 );


const rectLight3 = new THREE.RectAreaLight( 0xFF0000, 10, 1, 6 );
rectLight3.position.set( -3, 7, 4 );
rectLight3.rotateX(Math.PI/2)
rectLight3.rotateZ(Math.PI/2)
rectLight3.rotateY(5*(Math.PI)/6)
const rectLightHelper = new RectAreaLightHelper( rectLight3 );
//rectLight3.add( rectLightHelper );

scene.add( rectLight3 );

/** grid helper */
const size = 30;
const divisions = 30;
const gridHelper = new THREE.GridHelper( size, divisions );
scene.add( gridHelper );


/** _____________________________________________________________________ */

const geometryCone1 = new THREE.ConeGeometry(0.5,1,64,64);
const materialCone1 = new THREE.MeshBasicMaterial( {color: 0xff0000} );
const cone1 = new THREE.Mesh( geometryCone1, materialCone1 );

cone1.position.x = -3;
cone1.position.y = 6;
cone1.position.z = 4;
cone1.rotation.x = Math.PI;
//scene.add( cone1 );

/** ---------------------------- Curriculum vitae -------------------------------- */

const geometry = new THREE.PlaneGeometry( .7, 1 );
const material = new THREE.MeshBasicMaterial( {map : new THREE.TextureLoader().load('/models/textures/CV_tisserant_Paul_EN.png'), side: THREE.DoubleSide} );
const feuille = new THREE.Mesh( geometry, material );
const defaultpositionCV= new THREE.Vector3(-1.5, 2.7, 3.5);
feuille.position.set(defaultpositionCV.x,defaultpositionCV.y,defaultpositionCV.z)
feuille.rotation.x = Math.PI/2;

feuille.rotation.y = Math.PI;

scene.add( feuille );



/** ------------------------------------------------------------------------------- */

const loader = new GLTFLoader();

let model;

loader.load(
    // resource URL
    '/models/room/CleanedRoom.glb',
    // called when the resource is loaded
    function ( gltf ) {
        model = gltf.scene;

      scene.add( model );
      animate()
      console.log(model)
      raycasting();

    },
    // called while loading is progressing
    function ( xhr ) {

      console.log( ( xhr.loaded / xhr.total * 100 ) + '% loaded' );

    },
    // called when loading has errors
    function ( error ) {

      console.log( 'An error happened' );

    }
);
