function raycasting() {



  const raycaster = new THREE.Raycaster();
  const mouseClick = new THREE.Vector2();
  model.add(feuille);
  model.children[28].name = 'CV';
  console.log(model.children);

  /*  model.traverse((child) => {
        if (child.name === "cube022") {
            child.material = videoMaterial;
        }
    });


   */




/** Raycasting sur le model room */

const iterator = [];

model.children.forEach(eleme => {
    iterator.push(eleme);
})

//console.log(model.children.includes('screen1'));

let trigger = false;
let triggerEcran = false;

  window.addEventListener('click', event => {
    mouseClick.x = ( event.clientX / window.innerWidth ) * 2 - 1;
    mouseClick.y = - ( event.clientY / window.innerHeight ) * 2 + 1;

    raycaster.setFromCamera( mouseClick, camera );
    const foundInRoom = raycaster.intersectObjects( iterator );
    console.log(foundInRoom[0].object.name)

      if (foundInRoom[0].object.name === 'CV') {
          if (trigger === false && triggerEcran === false) {
              gsap.to(feuille.position, {
                  duration: 1,
                  z: controls.target.z,
                  x: controls.target.x ,
                  y: controls.target.y,
              });
              feuille.rotation.x = 0;

              feuille.scale.set(5,5,5);
              trigger = true;
          } else {
              gsap.to(feuille.position, {
                  duration: 1,
                  z : 3.5,
                  y: 2.7,
                  x: -1.5,
              });
              feuille.rotation.x = Math.PI/2;
              feuille.scale.set(1,1,1);
              trigger = false;
          }

      }
      if (foundInRoom[0].object.name === 'table') {
          if (scene.children.includes(rectLight3)) {
              scene.remove(rectLight3)
          }else {
              scene.add(rectLight3)
          }
      }

      if (foundInRoom[0].object.name === 'Plane002' || foundInRoom[0].object.name === 'Plane008') {
          if (triggerEcran === false) {
              gsap.to(controls.target, {
                  duration: 1,
                  y: 4,
                  x: -3,
                  z: 5,
              });

              gsap.to(camera.position, {
                  duration: 1,
                  y: 4,
                  x: -3,
                  z: 1.2,
              });

              controls.enableDamping = false;
              controls.enableZoom = false;
              controls.enableRotate = false;

              controls.update();
              triggerEcran = true;



          } else {

              gsap.to(controls.target, {
                  duration: 1,
                  y: 4,
                  x: 0,
                  z: 0,
              });
              gsap.to(camera.position, {
                  duration: 1,
                  y: 4,
                  x: 4,
                  z: 0,
              });

              controls.enableDamping = true;
              controls.enableZoom = true;
              controls.enableRotate = true;

              controls.update();
              triggerEcran = false;
          }
      }
  });


}








function animate() {
  requestAnimationFrame(animate)
  renderer.render(scene, camera)
  controls.update();
  //console.log(controls.target)
}


animate()


export { scene };